
.post {
    height: 100vh;
    width: 100%;
    overflow-y: scroll;
    box-sizing: border-box;
    position: relative;

    .read-line {
        position: fixed;
        top: 0;
        left: 0;
        height: 2px;
        background-color: black;
    }

    .header {
        background-color: transparent;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 0 5vw;
        padding-top: 2.8vw;
        color: #fff;
        z-index: 10;

        .logo {
            font-family: 'Avenir Next Cyr';
            font-weight: 300;
            font-size: 26px;
            cursor: pointer;
            transition: opacity 0.5s;

            &:hover {
                opacity: 0.5;
            }
        }
    }

    .head {
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-attachment: fixed;

        * {
            color: #fff;
        }

        h1 {
            font-size: 40px;
        }

        .tag.active,
        .title.active {
            animation-name: contentAppear;
            animation-duration: 1.5s;
            animation-fill-mode: forwards;
            animation-delay: 0.5s;
            opacity: 0;
        }
    }

    .content {
        box-sizing: border-box;
        width: 100%;
        padding: 40px 0;
    }

    .disable {
        opacity: 0;
    }

    .active {
        animation-name: contentAppear;
        animation-duration: 1.5s;
        animation-fill-mode: forwards;
    }

    @keyframes contentAppear {
        from {
            opacity: 0;
            transform: translateY(30px);
        }
        to {
            opacity: 1;
            transform: translateY(0);
        }
    }
}

.container {
    width: 100%;
    box-sizing: border-box;
}

.paragraph {


    &.initial p::first-letter {
        font-size: 34px;
    }

    p {
        width: 100%;
    }
}

.image {
    margin: 50px 0;

    img {
        display: block;
        width: 100%;
        height: auto;
    }
}

.panorama {
    margin: 50px 0;

    img {
        display: block;
        width: 100%;
        height: auto;
        
    }
}

.main-layer {
    position: relative;
    z-index: 1000;
}

.p {
    width: 100%;
    height: 500px;
    background-position: center;
}

/*==================================================
    media queries
==================================================*/

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .post {
        .header {
            padding: 0 8vw;
            padding-top: 6vw;
        }
        
        .head {
    
            * {
                color: #fff;
            }
    
            h1 {
                font-size: 24px;
                font-weight: 800;
            }

            h3 {
                font-size: 18;
            }
        }
    }

    .container {
        padding: 0 10%;
    }
}
/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) and (max-width: 767.98px) {
    .container {
        padding: 0 10%;
    }
}
/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .container {
        padding: 0 15%;
    }
}
/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1199.98px) {
    .container {
        padding: 0 20%;
    }
}
/* large devices+ (large desktops, 1200px and up)*/
@media (min-width: 1200px) and (max-width: 1599.98px) {
    .container {
        padding: 0 27.5%;
    }
}
/* Extra large devices (large desktops, 1600px and up)*/
@media (min-width: 1600px) {
    .container {
        padding: 0 30%;
    }
}