.home {
    width: 100vw;
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #222;
    background-size: 150%;
    background-position: center;
    position: relative;
    overflow: hidden;
    transition: background-image 0.5s;

    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #000;
        opacity: 0.7;
        z-index: 2;
    }

    .header {
        background-color: transparent;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 5vw;
        padding-top: 2.8vw;
        color: #fff;
        z-index: 10;

        .nav {

            ul {
                list-style: none;
                display: flex;
                padding: 0;
                margin: 0;

                li:first-child {
                    display: none;
                }

                li {
                    margin-right: 2vw;
                    font-family: 'Avenir Next Cyr';
                    font-weight: 300;
                    font-size: 16px;
                    text-transform: uppercase;
                    position: relative;
                    cursor: pointer;

                    a {
                        color: #fff;
                        text-decoration: none;

                        &:hover {
                            color: #b4a88f;
                        }
    
                        &:after {
                            display: block;
                            content: "";
                            background-color: #fff;
                            height: 1px;
                            width: 0%;
                            left: 50%;
                            bottom: -5px;
                            position: absolute;
                            -webkit-transition: width .3s ease-in-out;
                            -moz--transition: width .3s ease-in-out;
                            transition: width .3s ease-in-out;
                            -webkit-transform: translateX(-50%);
                            -moz-transform: translateX(-50%);
                            transform: translateX(-50%);
                        }
                    
                        &:hover:after,
                        &:focus:after {
                            width: 100%;
                        }

                    }
                }
            }
        }

        .logo {
            font-family: 'Avenir Next Cyr';
            font-weight: 300;
            font-size: 26px;
            cursor: pointer;
            transition: opacity 0.5s;

            &:hover {
                opacity: 0.5;
            }
        }
    }

    .main {
        justify-content: center;
        align-items: center;
    }

    .center {
        position: relative;
        width: 58vw;
        height: 33vw;
        z-index: 10;

        .backview {
            position: absolute;
            top: 0;
            left: 0;
            width: 64%;
            height: 84%;
            background-size: cover;
            z-index: 5;
            transition: background-image 0.5s ease-in-out 0.2s;
        }

        .preview {
            position: absolute;
            top: 25px;
            left: 25px;
            width: calc(100% - 50px);
            height: calc(100% - 50px);
            background-size: cover;
            z-index: 6;
            transition: background-image 0.5s ease-in-out;
        }

        .text {
            position: absolute;
            bottom: 0;
            right: 0;
            width: 40%;
            height: 69%;
            z-index: 7;
            background-color: #fff;
            padding: 1.6vw;
            overflow: hidden;
            box-sizing: border-box;

            h1,
            h2,
            h3,
            h4,
            h5 {
                margin: 0;
            }
            
            .tag {
                text-transform: uppercase;
                margin-bottom: 2px;
            }

            .title {
                margin-bottom: 10px;

                h1 {
                    text-transform: uppercase;
                    font-family: 'Avenir Next Cyr';
                    font-weight: 700;
                    line-height: 35px;
                    letter-spacing: 1px;
                }
            }

            .num {
                margin-bottom: 10px;

                span {
                    color: #b4a88f;
                }
            }

            .excerpt {
                margin-bottom: 10px;
            }

            .btn {
                span {
                    cursor: pointer;
                    position: relative;
                    text-transform: uppercase;
                    font-size: 16px;
                    font-weight: 500;
                    color: #000;
                    transition: color 0.5s;

                    &:hover {
                        color: #b4a88f;
                    }
                    
                    &:after {
                        display: block;
                        content: "";
                        background-color: #222;
                        height: 1px;
                        width: 0%;
                        left: 50%;
                        bottom: -5px;
                        position: absolute;
                        -webkit-transition: width .3s ease-in-out;
                        -moz--transition: width .3s ease-in-out;
                        transition: width .3s ease-in-out;
                        -webkit-transform: translateX(-50%);
                        -moz-transform: translateX(-50%);
                        transform: translateX(-50%);
                    }
                
                    &:hover:after,
                    &:focus:after {
                        width: 100%;
                    }
                }
            }
        }
    }

    .navigation-wrapper {
        position: absolute;
        right: 0;
        padding-right: 5vw;
        z-index: 9;
        display: flex;
        flex-direction: column;
        align-items: center;

        .arrow {
            width: 12px;
            cursor: pointer;
            transition: transform 0.5s;

            &.up {
                transform: rotate(180deg);
                margin: 20px 0;
            }

            &.up:hover {
                transform: rotate(180deg) translateY(5px);
            }

            &.down:hover {
                transform: translateY(5px);
            }
        }
    }

    .navigation {
        overflow: hidden;
        height: 300px;
        display: flex;
        flex-direction: column;

        ul {
            display: flex;
            flex-direction: column;
            list-style: none;
            padding: 0;
            margin: 0;
            transition: transform 0.4s;

            li {
                display: flex;
                align-items: center;
                height: 50px;
            }

            li span {
                cursor: pointer;
                position: relative;
                color: #fff;
                font-family: 'Avenir Next Cyr';
                font-weight: 100;
                font-size: 20px;

                &:hover {
                    color: #b4a88f;
                }

                &:after {
                    display: block;
                    content: "";
                    background-color: #fff;
                    height: 1px;
                    width: 0%;
                    left: 50%;
                    bottom: -5px;
                    position: absolute;
                    -webkit-transition: width .3s ease-in-out;
                    -moz--transition: width .3s ease-in-out;
                    transition: width .3s ease-in-out;
                    -webkit-transform: translateX(-50%);
                    -moz-transform: translateX(-50%);
                    transform: translateX(-50%);
                }
            
                &:hover:after,
                &:focus:after {
                    width: 100%;
                }
            }
        }
    }

    .particles-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 4;
    }

    // animations

    .center.leave .tag  {
        animation-name: textExit;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0s;
    }
    .center.enter .tag {
        animation-name: textEnter;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0s;
        opacity: 0;
    }

    .center.leave .title  {
        animation-name: textExit;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.1s;
    }
    .center.enter .title {
        animation-name: textEnter;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.1s;
        opacity: 0;
    }

    .center.leave .num  {
        animation-name: textExit;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.2s;
    }

    .center.enter .num {
        animation-name: textEnter;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.2s;
        opacity: 0;
    }

    .center.leave .excerpt  {
        animation-name: textExit;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.3s;
    }

    .center.enter .excerpt {
        animation-name: textEnter;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.3s;
        opacity: 0;
    }

    .center.leave .btn  {
        animation-name: textExit;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.4s;
    }

    .center.enter .btn  {
        animation-name: textEnter;
        animation-duration: 300ms;
        animation-fill-mode: forwards;
        animation-delay: 0.4s;
        opacity: 0;
    }

    @keyframes textExit {
        from {
            transform: translateX(0);
            opacity: 1;
        }
        to {
            transform: translateX(10px);
            opacity: 0;
        }
    }

    @keyframes textEnter {
        from {
            transform: translateX(-10px);
            opacity: 0;
        }
        to {
            transform: translateX(0);
            opacity: 1;
        }
    }

}


/*==================================================
    media queries
==================================================*/

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {

    .home {
        justify-content: flex-start;
        align-items: flex-end;
        background-size: cover;
        background-position: center;
    
        .header {
            align-items: baseline;
            padding: 0 8vw;
            padding-top: 6vw;
    
            .nav {
    
                ul {
                    flex-direction: column;

                    li {
                        margin-bottom: 10px;
                        opacity: 0;
                        transition: opacity 0.5s;
                        font-size: 12px;
                    }

                    li:first-child{
                        display: inline;
                        opacity: 1; 
                    }

                    li:not(:first-child) {
                        touch-action: none;
                    }

                    li.active { 
                        opacity: 1;
                    }
                }
            }

            .logo {
                font-size: 18px;
            }
        }

        .navigation-wrapper {
            display: none;
        }

        .center {
            width: 80vw;
            height: 55vh;
            padding-left: 8vw;
            z-index: 10;

            .backview {
                display: none;
            }

            .preview {
                display: none;
            }

            .text {
                position: static;
                width: 100%;
                height: 100%;
                padding: 0;
                background-color: transparent;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .tag {
                    margin-bottom: 2vh;

                    & * {
                        color: #fff;
                    }
                    
                }

                .title {
                    margin-bottom: 2vh;
                    & * {
                        color: #fff;
                    }

                    &:after {
                        content: "";
                        display: block;
                        width: 25px;
                        height: 3px;
                        background: #fff;
                        margin-top: 10px;
                    }
                }

                .num {
                    margin-bottom: 2vh;

                    & * {
                        color: #fff;
                    }
                }

                .excerpt {

                    margin-bottom: 2vh;

                    & * {
                        color: #fff;
                    }
                }

                .btn{
                    margin-bottom: 40px;
                    & * {
                        color: #fff !important;
                    }
                }
            }
        }
    }

}
