        .center {

            .text {

                .tag {

                    & * {
                        font-size: 21px;
                    }
                    
                }

                .title {

                    h1 {
                        font-size: 38px;
                        line-height: 44px;
                    }
                }

                .num {

                    h3 {
                        font-size: 21px;
                    }
                }

                .excerpt {

                    & > * {
                        font-size: 20px;
                    }
                }
            }
        }
    }
